import ListForm from '@/components/ListForm'
import ListOptionBtn from '@/components/ListOptionBtn'
export default {
  components: {
    ListForm,
    ListOptionBtn
  },
  data() {
    return {
      items: [], // 
      selectionList: [], // 
      pageSize: 10, // 
      currentPage: 1, // 
      totalSize: 0, // 
      searchKeyword: '', // 
      modelName: this.$route.path.match(/\/(\w+?)\//i)[1]
    }
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(
        `rest/${this.modelName}?page=${this.currentPage}&size=${
          this.pageSize
        }${this.searchKeyword ? '&search=' + this.searchKeyword : ''}`
      )
      this.items = res.data.items
      this.totalSize = res.data.totalCount
    },
    /*  */
    async remove(row) {
      this.$confirm(
        `"${row.name ? row.name : row.path}"?`,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      )
        .then(async () => {
          await this.$http.delete(`rest/${this.modelName}/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          })
          this.fetch() // 
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    /*  */
    async removeMany() {
      if (this.selectionList.length) {
        this.$confirm(
          `${this.selectionList.length}?`,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        )
          .then(async () => {
            await this.$http.delete(`rest/${this.modelName}/`, {
              data: this.selectionList
            })
            this.$message({
              type: 'success',
              message: '!'
            })
            this.fetch() // 
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: ''
            })
          })
      } else {
        this.$message({
          message: '',
          type: 'warning'
        })
      }
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      console.log(this.currentPage)
      this.fetch()
    },
    handleSearchedData(searchedData) {
      this.items = searchedData
    },
    /*  */
    handleSelectionChange(val) {
      this.selectionList = val
    }
  },
  created() {
    this.fetch()
  }
}
